.pricing{
    width:100%;
    padding: 6rem 1rem;
    background: black;

}

.card-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 40px;

}

.card{
    border:1px solid whitesmoke ;
    text-align: center;
    padding: 1rem;

}

.card:hover{
    background-color: #141414;
}

.card h3{
    font-size: 1.4rem;
    color: aliceblue;
    padding: 1rem;
}

.card p{
    padding:10px 0;
}

.bar{
    border-bottom: 1px solid wheat;
    width: 90%;
    margin: 1.5rem auto;
    display: block;
}

.btc{
    font-size: 4rem;
    font-weight: 600;
    padding: 1rem 0;
}
.card .btn{
    padding: 10px;
    display: block;
    width: 70%;
    margin: 2rem auto;
    text-align: center;
}

@media screen and (max-width:740px) {
    .card-container{
        max-width: 80%;
        max-height: 80%;
        margin: auto; 
        grid-template-columns: 1fr;    
    }
}