.footer{
    width: 100%;
    padding: 6rem 0;
    background-color: rgba(19, 19,19, 0.8);
}

.footer-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 80px;   
    /* 40px */
}
.left{
    height: 100%;
    width:100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.location{
    display: flex;
    margin-bottom: 0.8rem;
}
.location p{
    line-height: 30px;
}
.location p.india{
    text-align:start;
}
.right{
    height:100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding:1rem;

}

.social{
    margin-top: 1rem;
}

h4{
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
}
p{
    font-size: 1.2rem;
}

.phone{
    display: flex;
    margin: 0.8rem 0rem 0.8rem 0rem;
}
.email{
    display: flex;
    margin-bottom: 0.8rem;
    margin-top: 0.8rem;
}


@media screen and (max-width:640px){
    .footer-container{
        grid-template-columns: 1fr;
    }
}